@import '../../sass/utils/mixin';

.popUpOverlay {
  padding:  92px 16px 32px;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(39, 18, 12, 0.5);
  z-index: 9;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  overflow-y: auto;
}

.popUpContent {
  position: relative;
  padding: 66px 14px 24px;
  border-radius: 8px;
  background-color: rgba(240, 231, 218, 1);
  max-height: none;
  width: 100%;
  max-width: 500px;
}

.closeButton {
  position: absolute;
  top: 16px;
  right: 16px;
  background-color: transparent;
  border: none;
}

.closeIcon {
  width: 42px;
  height: 42px;
}

.popUpTitle {
  font-family: 'Montserrat Bold';
  font-size: 16px;
  line-height: 1.5;
  text-align: center;
  color: rgba(39, 18, 12, 1);
  margin-bottom: 8px;
}

.popUpDescription__border {
  border-bottom: 1px solid rgba(191, 119, 66, 1);
  margin-left: -16px;
  margin-right: -16px;
  margin-bottom: 4px;
}

.popUpDescription {
  font-family: 'Montserrat Regular';
  font-size: 16px;
  line-height: 1.5;
  text-align: center;
  color: rgba(39, 18, 12, 1);
  margin-bottom: 16px;
}

.labelTitle {
  font-family: 'Montserrat Regular';
  font-size: 16px;
  line-height: 1.5;
  padding: 4px;
  color: rgba(39, 18, 12, 1);
}

.popUplabel {
  font-family: 'Montserrat Regular';
  font-size: 14px;
  line-height: 1.43;
  background-color: rgba(240, 231, 218, 1);
  width: 100%;
  border-radius: 4px;
  border: 1px solid rgba(99, 83, 74, 1);
  padding: 10px 16px;
  margin-bottom: 8px;
}

.submitButton {
  width: 100%;
  display: block;
  margin-left: auto;
  margin-right: auto;
  font-family: 'Montserrat Bold';
  font-size: 16px;
  line-height: 24px;
  text-align: center;
  border: none;
  padding-top: 12px;
  padding-bottom: 8px;
  border-radius: 4px;
  background-color: var(--copperYellow);
  color: var(--eggShellWhite);
  margin-bottom: 8px;
}

.privacyText {
  font-family: 'Montserrat Regular';
  font-size: 14px;
  line-height: 1.43;
  color: rgba(99, 83, 74, 1);
}

.privacyLink {
  font-family: 'Montserrat Bold';
  font-size: 16px;
  line-height: 1.5;
  color: rgba(39, 18, 12, 1);
  border-bottom: 1px solid rgba(39, 18, 12, 1);
}

.privacyContainer {
  padding: 8px 24px;
}

.noScroll {
  overflow: hidden;
  position: relative;
  width: 100%;
}